  import React, { useState } from "react";
  import { Link, useLocation } from "react-router-dom";
  import SVG from "react-inlinesvg";
  import { toAbsoluteUrl } from "../../utils";
  import { OverlayTrigger, Tooltip } from "react-bootstrap";
  import FeedsList from "./feedsList";
  import DateRangePicker from "react-bootstrap-daterangepicker";
  import "bootstrap-daterangepicker/daterangepicker.css";
  import { shallowEqual, useDispatch } from "react-redux"; 
  import { useSelector } from "react-redux";
  import queryString from "query-string";
  import moment from "moment";
  import { activeTab, refreshButton, updateDateFilter, updateSelectedFeed } from "../../../../../actions/analyticsNew";
  import { useEffect } from "react";

  const Subheader = (props) => {
    const location = useLocation();
    const dispatch = useDispatch();

    const { analyticwall, analyticsStatus, pageRefresh } = useSelector(
      ({ analyticwallD, analyticsStatusD, analyticsRecords }) => ({
        analyticwall: analyticwallD,
        analyticsStatus: analyticsStatusD,
        pageRefresh: analyticsRecords.page_refresh,
      }),
      shallowEqual
    );
 
    const filterDate =
      analyticwall && Object.keys(analyticwall).length > 0
        ? analyticwall.dateFilter
        : null;
    let startdateupdate =
      filterDate && Object.keys(filterDate).length > 0 && filterDate.startDate
        ? filterDate.startDate
        : moment().subtract(29, "days").format("DD-MM-YYYY");
    let enddateupdate =
      filterDate && Object.keys(filterDate).length > 0 && filterDate.endDate
        ? filterDate.endDate
        : moment().format("DD-MM-YYYY");
    const parsed = queryString.parse(location.search);
    if (parsed.startdate) {
      startdateupdate = moment.unix(parsed.startdate).format("DD-MM-YYYY");
    }
    if (parsed.enddate) {
      enddateupdate = moment.unix(parsed.enddate).format("DD-MM-YYYY");
    }

    const [startDate, setStartDate] = useState(startdateupdate);
    const [endDate, setEndDate] = useState(enddateupdate);
    const [maxDate, setMaxDate] = useState(
      moment().endOf("month").format("DD-MM-YYYY")
    );

    console.log(analyticwall.selectedFeed,'analyticwallanalyticwallanalyticwall')

    const handleOnApply = (event, picker) => { 
      dispatch(
        updateSelectedFeed(analyticwall.selectedFeed,analyticsStatus.trialAccessAPI,
          { startDate: picker.startDate, endDate: picker.endDate }
        )
      );
    setStartDate(picker.startDate.format("DD-MM-YYYY"));
      setEndDate(picker.endDate.format("DD-MM-YYYY"));
    };

    useEffect(() => {
       dispatch(activeTab(location.pathname))
    }, []);

  

    const onClickRefreshButton = event => {
       dispatch(refreshButton(analyticsStatus.trialAccessAPI))
    }

    return (
      <div> 
      <div id="sw_subheader" className="subheader analytics_subheader">
        <div className="container-fluid px-xl-5">
          <div className="row align-items-center justify-content-between py-3 flex-wrap">
            <div className="col-sm-4">
              <div className="d-flex align-items-center flex-grow-1">
                <ul className="nav sw-anlts-nav-tabs"> 
                  <li className="me-3">   
                    <Link
                      to={`/analytic/${localStorage.getItem('wallId')}`}
                      className={`nav-link ${
                          location.pathname.includes(localStorage.getItem('wallId')) ? `active` : ``
                      }`}
                    >
                      Content
                    </Link>
                  </li>
                  <li className="me-3">
                    <Link
                      to={"/analytic/activityHub/widget"}
                      className={`nav-link ${
                        location.pathname.includes("activityHub")
                          ? `active`
                          : ``
                      }`}
                    >
                      Activity Hub
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
            <div className="col-sm-8">
              <div className="d-flex justify-content-sm-end">

                <div className="ms-3">
                  <OverlayTrigger
                    placement="bottom"
                    overlay={<Tooltip>Reffresh</Tooltip>}
                  >
                    <button
                      onClick={()=>onClickRefreshButton()}
                      className="btn btn-outline-secondary py-2 px-4"
                      type="button"
                    >
                     <span className={`svg-icon svg-icon-sm svg-icon-dark-75 me-0 ${(pageRefresh && pageRefresh) ? `fa fa-spin` : ``}`}>
                       <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24">
                          <g id="Group_4474" data-name="Group 4474" transform="translate(10951 -4712)">
                            <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(-10951 4712)" fill="none" opacity="0"></rect>
                            <g id="Group_1" data-name="Group 1" transform="translate(-10950 4715)">
                              <path
                                id="Path_17"
                                data-name="Path 17"
                                d="M17.617,40.968a6.864,6.864,0,0,1-3.843,1.155,6.966,6.966,0,0,1-6.929-6.059H8a1.027,1.027,0,0,0,.869-.474.9.9,0,0,0,0-.967L6.656,30.974a1,1,0,0,0-.869-.474h0a1.027,1.027,0,0,0-.869.474c-.778,1.25-1.933,3.277-2.122,3.556a1.344,1.344,0,0,0-.189.566.916.916,0,0,0,.961.967H4.814a8.994,8.994,0,0,0,8.962,7.993,8.833,8.833,0,0,0,4.9-1.438.955.955,0,0,0,.283-1.344,1.02,1.02,0,0,0-1.342-.307Z"
                                transform="translate(-2.605 -26.138)"
                                fill="#545454"
                              ></path>
                              <path
                                id="Path_18"
                                data-name="Path 18"
                                d="M42.874,19.993h-1.25A8.993,8.993,0,0,0,32.666,12a8.833,8.833,0,0,0-4.9,1.438.971.971,0,1,0,1.061,1.626,6.864,6.864,0,0,1,3.843-1.155A6.966,6.966,0,0,1,39.6,19.969H38.444a1.027,1.027,0,0,0-.869.474.9.9,0,0,0,0,.967l2.213,3.654a1,1,0,0,0,.869.474h0a1.027,1.027,0,0,0,.869-.474c.778-1.25,1.933-3.277,2.122-3.556a1.344,1.344,0,0,0,.189-.566.923.923,0,0,0-.963-.949Z"
                                transform="translate(-21.494 -12)"
                                fill="#545454"
                              ></path>
                            </g>
                          </g>
                        </svg>
                      </span>  
                    </button>
                  </OverlayTrigger>
                </div>

               { !props.activeTab ? <div className="ms-3">
                  <FeedsList />
                </div> : null }
                <div className="ms-3">
                  <OverlayTrigger
                    placement="bottom"
                    overlay={<Tooltip>Print</Tooltip>}
                  >
                    <button
                      className="btn btn-outline-secondary py-2 px-4"
                      type="button"
                    >
                      <SVG
                        className="me-1"
                        height={"16px"}
                        width={"16px"}
                        src={toAbsoluteUrl("/svg/print.svg")}
                      ></SVG>{" "}
                      Print
                    </button>
                  </OverlayTrigger>
                </div>
                <div className="ms-3">
                  <OverlayTrigger
                    placement="bottom"
                    overlay={<Tooltip>Export</Tooltip>}
                  >
                    <button
                      className="btn btn-outline-secondary py-2 px-4"
                      type="button"
                    >
                      <SVG
                        className="me-1"
                        height={"16px"}
                        width={"16px"}
                        src={toAbsoluteUrl("/svg/export.svg")}
                      ></SVG>{" "}
                      Export
                    </button>
                  </OverlayTrigger>
                </div>

                <div className="ms-3">
                  <DateRangePicker
                    onApply={handleOnApply}
                    startDate={startDate}
                    endDate={endDate}
                    initialSettings={{
                      locale: { format: "DD-MM-YYYY", separator: "-" },
                      maxDate: maxDate,
                      showDropdowns: true,
                      startDate: startDate,
                      endDate: endDate,
                      ranges: {
                        Today: [
                          moment().format("DD-MM-YYYY"),
                          moment().format("DD-MM-YYYY"),
                        ],
                        Yesterday: [
                          moment().subtract(1, "days").format("DD-MM-YYYY"),
                          moment().subtract(1, "days").format("DD-MM-YYYY"),
                        ],
                        "Last 7 Days": [
                          moment().subtract(6, "days").format("DD-MM-YYYY"),
                          moment().format("DD-MM-YYYY"),
                        ],
                        "Last 30 Days": [
                          moment().subtract(29, "days").format("DD-MM-YYYY"),
                          moment().format("DD-MM-YYYY"),
                        ],
                        "This Month": [
                          moment().startOf("month").format("DD-MM-YYYY"),
                          moment().endOf("month").format("DD-MM-YYYY"),
                        ],
                        "Last Month": [
                          moment()
                            .subtract(1, "month")
                            .startOf("month")
                            .format("DD-MM-YYYY"),
                          moment()
                            .subtract(1, "month")
                            .endOf("month")
                            .format("DD-MM-YYYY"),
                        ],
                      },
                    }}
                    // onCallback={handleCallback}
                  >
                    <button type="button" className="btn btn-outline-secondary py-2 px-4 btn-icon" id="reportrange">
                      <OverlayTrigger
                        placement="bottom"
                        overlay={<Tooltip>Date Filter</Tooltip>}
                      >
                        <div className="btn btn-outline-secondary py-2 px-4 btn-icon">
                          <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                            <SVG
                              className="h-75 align-self-end"
                              src={toAbsoluteUrl("/svg/calendar.svg")}
                            ></SVG>
                          </span>

                        </div>
                      </OverlayTrigger>
                    </button>
                  </DateRangePicker>
                </div>
              </div>
            </div>
            
          </div>
        </div>
      </div>

      
        {/* <div  className="d-flex flex-column-fluid main_content">
            <div className="py-5 w-100">
                <div className="container-fluid px-xl-5">
                      <div className="row">
                        <div className="col-lg-12 col-xxl-12">
                            <div className="alert custom-alert">
                                <span>This is a demo data. You cannot see live data because you haven't added a post yet. Please add posts to see real time analytics.</span>
                                  <a className="free-trial-upgrade cursor-pointer" onClick={()=>dispatch(togglePlanSection(true))}>
                                    Upgrade Now
                                </a> 
                            </div>
                        </div>
                    </div>  
                </div>
            </div>
        </div>           */}
      </div>
    );
  };

  export default Subheader;
